import React, { Component } from 'react';
import '../css/chatWindow.css';
import { resumeProfile } from '../data/resumeProfile.js';
import { systemPrompt } from '../data/systemPrompt.js';

const OPENAI_API_KEY = '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';
// Using the Responses API (recommended over Chat Completions)
const OPENAI_API_URL = 'https://api.openai.com/v1/responses';

export class ChatWindow extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentPromptIndex: 0,
            inputValue: '',
            isFocused: false,
            messages: [],
            isLoading: false
        };
        this.prompts = [
            "I can help you get to know Travis",
            "What are your qualifications?",
            "Tell me about your experience",
            "What technologies do you work with?",
            "What projects have you worked on?",
            "What are your interests?",
            "What's your background?",
            "What skills do you have?",
            "What kind of work do you do?"
        ];
        this.intervalId = null;
    }

    componentDidMount() {
        // Cycle through prompts every 3 seconds
        this.intervalId = setInterval(() => {
            if (!this.state.isFocused && !this.state.inputValue) {
                this.setState(prevState => ({
                    currentPromptIndex: (prevState.currentPromptIndex + 1) % this.prompts.length
                }));
            }
        }, 3000);
    }

    componentWillUnmount() {
        if (this.intervalId) {
            clearInterval(this.intervalId);
        }
    }

    handleFocus = () => {
        this.setState({ isFocused: true });
    };

    handleBlur = () => {
        this.setState({ isFocused: false });
    };

    handleChange = (e) => {
        this.setState({ inputValue: e.target.value });
    };

    handleSubmit = async (e) => {
        e.preventDefault();
        const message = this.state.inputValue.trim();
        
        if (!message) return;

        // Add user message to chat
        const userMessage = { role: 'user', content: message };
        const updatedMessages = [...this.state.messages, userMessage];
        
        this.setState({ 
            inputValue: '', 
            messages: updatedMessages,
            isLoading: true 
        });

        try {
            const response = await fetch(OPENAI_API_URL, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${OPENAI_API_KEY}`
                },
                body: JSON.stringify({
                    model: "gpt-4o-mini",
                    input: [
                        { role: "system", content: systemPrompt },
                        {
                            role: "system",
                            content: `Background info:\n${JSON.stringify(resumeProfile, null, 2)}`
                        },
                        ...updatedMessages.map(msg => ({
                            role: msg.role,
                            content: msg.content
                        }))
                    ],
                    temperature: 0.3,
                })
            });

            if (!response.ok) {
                const errorText = await response.text();
                console.error('API Error:', errorText);
                throw new Error(`HTTP error! status: ${response.status}`);
            }

            const data = await response.json();

            // Responses API structure - extract content from response
            // The Responses API returns: data.output[0].content[0].text
            let aiContent;
            
            // Check for Responses API format first (output array structure)
            if (data.output && Array.isArray(data.output) && data.output.length > 0) {
                const firstOutput = data.output[0];
                if (firstOutput.content && Array.isArray(firstOutput.content) && firstOutput.content.length > 0) {
                    // Find the first content item with text
                    const textContent = firstOutput.content.find(item => 
                        item.type === 'output_text' && item.text
                    );
                    if (textContent && textContent.text) {
                        aiContent = textContent.text;
                    } else if (firstOutput.content[0].text) {
                        // Fallback: try first content item's text property
                        aiContent = firstOutput.content[0].text;
                    }
                }
            } 
            // Fallback to Chat Completions format
            else if (data.choices && data.choices[0] && data.choices[0].message) {
                const messageContent = data.choices[0].message.content;
                if (typeof messageContent === 'string') {
                    aiContent = messageContent;
                } else if (typeof messageContent === 'object' && messageContent.text) {
                    aiContent = messageContent.text;
                }
            } 
            // Other fallback formats
            else if (data.content) {
                if (typeof data.content === 'string') {
                    aiContent = data.content;
                } else if (typeof data.content === 'object' && data.content.text) {
                    aiContent = data.content.text;
                }
            } else if (data.message && data.message.content) {
                const messageContent = data.message.content;
                if (typeof messageContent === 'string') {
                    aiContent = messageContent;
                } else if (typeof messageContent === 'object' && messageContent.text) {
                    aiContent = messageContent.text;
                }
            }
            
            // Final safeguard: ensure we have a string
            if (!aiContent || typeof aiContent !== 'string') {
                console.error('Failed to extract text from API response:', data);
                aiContent = 'Sorry, I received an unexpected response format. Please try again.';
            }

            // Add AI response to chat
            const aiMessage = { role: 'assistant', content: aiContent };
            this.setState(prevState => ({
                messages: [...prevState.messages, aiMessage],
                isLoading: false
            }));
        } catch (error) {
            console.error('Error sending message:', error);
            const errorMessage = { role: 'assistant', content: 'Sorry, I encountered an error. Please try again.' };
            this.setState(prevState => ({
                messages: [...prevState.messages, errorMessage],
                isLoading: false
            }));
        }
    };

    render() {
        const { currentPromptIndex, inputValue, isFocused, messages, isLoading } = this.state;
        const currentPrompt = this.prompts[currentPromptIndex];
        const showPlaceholder = !inputValue && !isFocused;

        return (
            <div className="ChatWindow">
                <div className="ChatWindow-container">
                   
                    <div className="ChatWindow-header">
                        <div className="ChatWindow-status">
                            <span className="status-dot"></span>
                            <span className="status-text">AI Assistant</span>
                        </div>
                    </div>
                    <div className="ChatWindow-body">
                        {messages.length === 0 ? (
                            <div className="ChatWindow-message">
                                <p>Hi! I'm here to help you learn more about Travis. Ask me anything!</p>
                            </div>
                        ) : (
                            messages.map((msg, index) => (
                                <div key={index} className={`ChatWindow-message ${msg.role === 'user' ? 'user-message' : ''}`}>
                                    <p>{msg.content}</p>
                                </div>
                            ))
                        )}
                        {isLoading && (
                            <div className="ChatWindow-message">
                                <p className="typing-indicator">...</p>
                            </div>
                        )}
                    </div>
                    <form className="ChatWindow-input-container" onSubmit={this.handleSubmit}>
                        <input
                            type="text"
                            className="ChatWindow-input"
                            value={inputValue}
                            onChange={this.handleChange}
                            onFocus={this.handleFocus}
                            onBlur={this.handleBlur}
                            placeholder={showPlaceholder ? currentPrompt : "Type your message..."}
                            disabled={isLoading}
                        />
                        <button type="submit" className="ChatWindow-send" disabled={!inputValue.trim() || isLoading}>
                            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                <line x1="22" y1="2" x2="11" y2="13"></line>
                                <polygon points="22 2 15 22 11 13 2 9 22 2"></polygon>
                            </svg>
                        </button>
                    </form>
                </div>
            </div>
        );
    }
}
